import React from 'react';

class Home extends React.Component {
    render() {
        return (
            <div id="contents-center">
                <div id="calendar-wrapper" className="content">
                    <div className="title-left">
                        <h5> &amp; </h5>
                    </div>
                    <div className="title-right">
                        <h5>2018 5</h5>
                    </div>
                    
                    <table id="calendar-tb">
                        <tbody>
                            <tr>
                                <th>25(Fri)</th>
                                <th>26(Sat)</th>
                                <th>27(Sun)</th>
                                <th>28(Mon)</th>
                                <th>29(Tue)</th>
                                <th>30(Wed)</th>
                                <th>31(Thu)</th>
                            </tr>
                            <tr>
                                <td></td>
                                <td>77 </td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td>5 ~</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div id="soundbox-wrapper" className="content">
                    <div id="soundbox-title">NOTICE</div>
                    <div id="soundbox-contents-wrapper">
                        <div id="soundbox-content" className="content">    
                            <h5>77  </h5>
                            <p> (5/26) 13 30 28 101 76  . 
                                77  13:30  14:30  15:00  15:30  16:30 
                                 1 17:30   18:00~19:00 ()  19:00~19:30 () 
                                 2 19:30  21:00     22:00  22:30  , 
                                      .   .
                            </p>
                        </div>
                    </div>
                    <div id="soundbox-img"></div>
                </div>
            </div>
        );
    }
}

export default Home


