import React from 'react';

// [TODO] Make defalt state & Make Oject contain UserInfo

const SignUpComponent = ({id, password, passwordCf, username, aaaNum, schoolNum, major, email, mobile, handleChange, postSignUp, profile, uploadFile }) => {
    return (
        <div id="sign-up-wrapper">
                <h2></h2>
                <table>
                    <thead></thead>
                    <tbody>
                        <tr>
                            <td colSpan="2">
                                <div className="div-agreement">
                                    <p>
                                        ?    - AAA -    !
                                        <br/><br/>
                                            AAA   ,
                                                   .
                                        <br/><br/>
                                               .
                                        <br/><br/><br/>
                                        1.   .
                                        <br/><br/>
                                                 ,  .
                                        <br/><br/><br/>
                                        2.    .
                                        <br/><br/>
                                               
                                                 .
                                        <br/><br/><br/>
                                          
                                        <br/><br/>
                                        Lv10 : .       .
                                        <br/>
                                        Lv9 : AAA    .   .
                                        <br/>
                                        Lv8 :   AAA .      .
                                        <br/>
                                        Lv7 : AAA .   .
                                        <br/><br/>

                                        AAA       .
                                                    .
                                           Lv 8  .
                                             Lv 7 .
                                        <br/><br/>
                                        , AAA   AAA    ,
                                           .
                                        <br/><br/>
                                        , AAA      ,
                                                  .
                                        <br/><br/>
                                        3.     ,    .
                                              .
                                         Lv 9  .
                                        <br/><br/>
                                        AAA    . .
                                        <br/>
                                           snuaaa@gmail.com   .
                                    </p>
                                </div>
                                <p className="enif-text-right">    .<input type="checkBox" /></p>
                            </td>
                        </tr>
                        <tr>
                            <td>*</td>
                            <td>
                                <input 
                                type="text"
                                name="id"
                                className="input-text"
                                onChange={handleChange}
                                value={id}
                                required="required" />
                            </td>
                        </tr>
                        <tr>
                            <td>*</td>
                            <td>
                                <input
                                type="password"
                                name="password"
                                className="input-text"
                                onChange={handleChange}
                                value={password}
                                required="required" />
                            </td>
                        </tr>
                        <tr>
                            <td> *</td>
                            <td>
                                <input
                                type="password"
                                name="passwordCf"
                                className="input-text"
                                onChange={handleChange}
                                value={passwordCf}
                                required="required" />
                            </td>
                        </tr>
                        <tr>
                            <td>*</td>
                            <td>
                                <input
                                type="text"
                                name="username"
                                className="input-text"
                                onChange={handleChange}
                                value={username}
                                required="required" />
                            </td>
                        </tr>
                        <tr>
                            <td> </td>
                            <td>
                                <input
                                type="text"
                                name="aaaNum"
                                className="input-text"
                                onChange={handleChange}
                                value={aaaNum} />
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <input
                                type="text"
                                name="schoolNum"
                                className="input-text"
                                onChange={handleChange}
                                value={schoolNum} />
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <input
                                type="text"
                                name="major"
                                className="input-text"
                                onChange={handleChange}
                                value={major} />
                            </td>
                        </tr>
                        <tr>
                            <td>E-mail*</td>
                            <td>
                                <input
                                type="email"
                                name="email"
                                className="input-text"
                                onChange={handleChange}
                                value={email}
                                required="required" />
                            </td>
                        </tr>
                        <tr>
                            <td>Mobile*</td>
                            <td>
                                <input
                                type="text"
                                name="mobile"
                                className="input-text"
                                onChange={handleChange}
                                value={mobile}
                                required="required" />
                                {/* -
                                <input type="text" />-
                                <input type="text" /> */}
                            </td>
                        </tr>
                        <tr>
                            <td>Profile</td>
                            <td>
                                <input
                                type="file"
                                name="profile"
                                accept="image/*"
                                onChange={(e) => uploadFile(e)}
                                /* ref={profileRef} */
                                value={profile}/>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td><textarea /></td>
                        </tr>
                        <tr>
                            <td><button ></button></td>
                            <td><button onClick={postSignUp}></button></td>
                        </tr>
                    </tbody>
                </table>
            </div>
    )
}


export default SignUpComponent;


