import React, { Component } from 'react';
import './App.css';
import Header from './components/Header'
import Aside from './components/Aside';
import Section from './containers/Section/Section';
import Footer from './components/Footer';
import { withRouter } from 'react-router';
import { loginCheck } from './actions';
import { connect } from 'react-redux';

const TAG = 'App'

class App extends Component {

    //[TODO] constructor token vaild  login state  .(  )
    // App component  router    header .   
    constructor(props) {
        console.log(`[%s] constructor`, TAG)
        super(props);
        this.checkToken();
    }

    
    componentDidMount() {
        console.log(`[%s] componentDidMount`, TAG)
    }

    checkToken = () => {
        console.log(`[%s] checkToken`, TAG)
        const token = localStorage.getItem('token')
        if(!token){
            //  logout
        }
        else {
            //    , invalid => logout, valid =>  ()
            this.props.onLoginCheck();
        }
    }



    render() {
        return (
            <div>
                <Header /> 
                <div id="contents-wrapper">
                    <Aside class="aside-left" />
                    <Section/>
                    {/* <Aside class="aside-right" /> */}
                </div>
                <Footer />
            </div>
        );
    }
}

// not used
const mapStateToProps = (state) => {
    return {
        loginState: state.authentication.isLoggedIn
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onLoginCheck: () => dispatch(loginCheck())
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
//export default connect(undefined, mapDispatchToProps)(App);


